'use strict';

/**
 * The global namespace for all Lost Marble functions, classes, and globals.
 * @namespace
*/
//let lm = {};
(function(global)
{
	global.lm = {};
})(this);

/**
 * An application can set this to false for release builds.
 * It controls things like console output and other debug testing.
 * @type {boolean}
 */
lm.DEBUG = true;

/**
 * A very small number for testing if a value is basically zero.
 * @type {number}
 */
lm.TINY = 1e-6;

/**
 * A very large number.
 * @type {number}
 */
lm.HUGE = 1e6;

/**
 * The vaue of pi.
 * @type {number}
 */
lm.PI = Math.PI;

/**
 * The value of pi * 2.
 * @type {number}
 */
lm.PI2 = Math.PI * 2.0;

/**
 * The value of pi / 2.
 * @type {number}
 */
lm.PIOVER2 = Math.PI / 2.0;

