lm._MouseDownCount_ = 0;

/**
 * Tests whether the mouse is down.
 * @returns {boolean} true if the mouse is down, otherwise false
*/
lm.IsMouseDown = function()
{
	return (lm._MouseDownCount_ > 0);
}

lm.FocusedInput = null;

//***********************************************************************************
//*** Simple One Argument Functions
//***********************************************************************************

/**
 * Absolute value of a number.
 * @param {number} a A number
 * @returns {number} The absolute value of a
 */
lm.Abs = function(a)
{
	if (a < 0)
		return -a;
	return a;
}

/**
 * Rounds a number to the nearest integer.
 * @param {number} a A number
 * @returns {number} The value of a rounded to the nearest integer.
 */
lm.Round = function(a)
{
	if (a > 0)
		return parseInt(a + 0.5);
	return -parseInt(0.5 - a);
}

/**
 * Returns the binary sign of a number.
 * @param {number} a A number
 * @returns {number} The binary sign of the number, either -1, or 1 if the number is >= 0.
 */
lm.Sgn = function(a)
{ // return the binary sign of a, either -1, or 1 if >= 0
	if (a < 0)
		return -1;
	return 1;
}

/**
 * Returns the sign of a number.
 * @param {number} a A number
 * @returns {number} The sign of the number, either -1, 0, or 1.
 */
lm.Zsgn = function(a)
{ // return the sign of a, either -1, 0, or 1
	if (a < 0)
		return -1;
	else if (a > 0)
		return 1;
	return 0;
}

/**
 * Returns a floating point random number between 0 and 1.
 * Quick and dirty. For better control over the random sequence, see the lm.RandomSequence class
 * @returns {number} A floating point random number between 0 and 1.
 */
lm.Frandom = function()
{ // random number between 0 and 1
	return Math.random();
}

/**
 * Returns a floating point random number between -1 and 1.
 * Quick and dirty. For better control over the random sequence, see the lm.RandomSequence class
 * @returns {number} A floating point random number between -1 and 1.
 */
lm.FrandomSgn = function()
{ // random number between -1 and 1
	return Math.random() * 2.0 - 1.0;
}

/**
 * Returns an integer random number between min and max.
 * @param {number} min The lower integer in the range.
 * @param {number} max The upper integer in the range.
 * @returns {number} An integer random number between min and max.
 */
lm.RandomInt = function(min, max)
{ // random integer between min and max
	return min + lm.Floor(Math.random() * (0.999 + max - min));
}

/**
 * Returns a random number between min and max.
 * @param {number} min The lower float in the range.
 * @param {number} max The upper float in the range.
 * @returns {number} A random number between min and max.
 */
lm.RandomFloat = function(min, max)
{ // random float between min and max
	return min + Math.random() * (max - min);
}

/**
 * Returns a random true/false value.
 * @param {number} odds The odds of a true result. Ranges from 0 to 1.
 * @returns {boolean} A random boolean value.
 */
lm.RandomBool = function(odds)
{
	return (lm.RandomFloat(0.0, 1.0) < odds);
}

/**
 * Returns the nearest integer less than or equal to a floating point number.
 * @param {number} a A number
 * @returns {number} The nearest integer less than or equal to a.
 */
lm.Floor = function(a)
{
	if (a < 0)
		return parseInt(a - 1);
	return parseInt(a);
}

/**
 * Returns the nearest integer greater than or equal to a floating point number.
 * @param {number} a A number
 * @returns {number} The nearest integer greater than or equal to a.
 */
lm.Ceil = function(a)
{
	if (a < 0)
		return parseInt(a);
	return parseInt(a + 1);
}

/**
 * Modulo operation that works for both positive and negative numbers.
 * The basic % operator does not do the right thing for negative numbers.
 * @param {number} x A number
 * @param {number} n The modulo
 * @returns {number} x modulo n
 */
lm.Mod = function(x, n)
{
	return ((x % n + n) % n);
}

/**
 * Converts degrees to radians.
 * @param {number} a An angle in degrees.
 * @returns {number} The same angle in radians.
 */
lm.Rad = function(a)
{
	return a * Math.PI / 180.0;
}

/**
 * Converts radians to degrees.
 * @param {number} a An angle in radians.
 * @returns {number} The same angle in degrees.
 */
lm.Deg = function(a)
{
	return a * 180.0 / Math.PI;
}

//***********************************************************************************
//*** Simple Multi-Argument Functions
//***********************************************************************************

/**
 * Returns the smaller of two numbers.
 * @param {number} a A number
 * @param {number} b A number
 * @returns {number} The smaller of a and b.
 */
lm.Min = function(a, b)
{
	return a < b ? a : b;
}

/**
 * Returns the larger of two numbers.
 * @param {number} a A number
 * @param {number} b A number
 * @returns {number} The larger of a and b.
 */
lm.Max = function(a, b)
{
	return a > b ? a : b;
}

/**
 * Linear interpolation.
 * @param {number} a A number used as a percentage. Typically between 0 and 1.
 * @param {number} l A number corresponding to 0 percent.
 * @param {number} h A number corresponding to 1 percent.
 * @returns {number} A linearly interpolated number between l and h.
 */
lm.Lerp = function(a, l, h)
{
	// linear interpolation from l (when a == 0) to h (when a == 1)
	// (equal to (a * h) + ((1 - a) * l)
	return l + (h - l) * a;
}

/**
 * Smooth interpolation.
 * @param {number} a A number used as a percentage. Typically between 0 and 1.
 * @param {number} l A number corresponding to 0 percent.
 * @param {number} h A number corresponding to 1 percent.
 * @returns {number} A smoothly interpolated number between l and h.
 */
lm.Slerp = function(percent, l, h)
{
	// Smooth interpolation between 2 values:
	// Slopes at start and end of curve are 0.
	let a = 3.0 * percent * percent - 2.0 * percent * percent * percent;
	return lm.Lerp(a, l, h);
}

/**
 * Spline interpolation.
 * @param {number} xVal A float between min.x and max.x
 * @param {lm_Vector2} preVal An lm.Vector2 object
 * @param {lm_Vector2} min An lm.Vector2 object
 * @param {lm_Vector2} max An lm.Vector2 object
 * @param {lm_Vector2} postVal An lm.Vector2 object
 * @returns {number} A smoothly interpolated number between min.y and max.y.
 */
lm.SplineInterp4 = function(xVal, preVal, min, max, postVal)
{
	let inA, inB, tempVec;

	let f = max.y - min.y;
	if (lm.Abs(f) < lm.TINY)
	{ // special case 1 - min and max have the same y-value
		tempVec = max.Minus(min).Times(0.33333333);
		inA = min.Plus(tempVec);
		inB = max.Minus(tempVec);
	}
	else
	{
		f = min.y - preVal.y;
		if (lm.Abs(f) < lm.TINY)
		{ // special case 2 - min and preVal have the same y-value
			tempVec = min.Minus(preVal).Times(0.33333333);
			inA = min.Plus(tempVec);
		}
		else
		{
			tempVec = max.Minus(preVal);
			tempVec.DivEq(tempVec.x);
			tempVec.TimesEq((max.x - min.x) * 0.333333);
			inA = min.Plus(tempVec);
		}

		f = postVal.y - max.y;
		if (lm.Abs(f) < lm.TINY)
		{ // special case 3 - max and postVal have the same y-value
			tempVec = postVal.Minus(max).Times(0.33333333);
			inB = max.Minus(tempVec);
		}
		else
		{
			tempVec = postVal.Minus(min);
			tempVec.DivEq(tempVec.x);
			tempVec.TimesEq((max.x - min.x) * 0.333333);
			inB = max.Minus(tempVec);
		}
	}

	// These next lines prevent "overshoot" by restricting the interpolation
	// control points to be within the range of the actual key values.
	// The ideal solution might be to adjust the control points on both sides
	// so that the curve doesn't overshoot, but still maintains continuity.
	let yMin = lm.Min(min.y, max.y);
	let yMax = lm.Max(min.y, max.y);
	inA.y = lm.Clamp(inA.y, yMin, yMax);
	inB.y = lm.Clamp(inB.y, yMin, yMax);

	// Start at a t-value of xVal, then refine it by back-calculating the actual x value
	let t = xVal;
	/*int32	count;
	real	newX;
	count = 0;
	do
	{
		f = (t - min.x) / (max.x - min.x);
		newX = f * f * f * max.x;
		newX += 3.0f * f * f * (1.0f - f) * inB.x;
		newX += 3.0f * f * (1.0f - f) * (1.0f - f) * inA.x;
		newX += (1.0f - f) * (1.0f - f) * (1.0f - f) * min.x;
		t += (xVal - newX) * 0.5f;
		count++;
		if (count > 2)
			break;
	} while (LM_FABS(newX - xVal) > 0.5f);*/

	f = (t - min.x) / (max.x - min.x);
	let res = f * f * f * max.y;
	res += 3.0 * f * f * (1.0 - f) * inB.y;
	res += 3.0 * f * (1.0 - f) * (1.0 - f) * inA.y;
	res += (1.0 - f) * (1.0 - f) * (1.0 - f) * min.y;

	return res;
}

/**
 * Clamps a number between a specified range.
 * @param {number} v A number to be clamped.
 * @param {number} l The low end of the range.
 * @param {number} h The high end of the range.
 * @returns {number} A number clamped such that l <= result <= h.
 */
lm.Clamp = function(v, l, h)
{
	if (v < l)
		return l;
	if (v > h)
		return h;
	return v;
}

/**
 * Tests if two floating point numbers are equal.
 * @param {number} r1 A number.
 * @param {number} r2 A second number.
 * @param {number} [tolerance=lm.TINY] The tolerance to consider for "equals".
 * @returns {number} Returns true if r1 and r2 are within a tolerance. Otherwise returns false.
 */
lm.RealEqual = function(r1, r2, tolerance)
{
	tolerance = lm.Default(tolerance, lm.TINY);
	let f = r1 - r2;
	if (lm.Abs(f) < tolerance)
		return true;
	else
		return false;
}

/**
 * Used to set up a default value for a variable.
 * @param {any} variable Some variable.
 * @param {any} value The default value for that variable if it is undefined.
 * @returns {number} Returns "variable" if it is defined, otherwise returns value.
 * @example
 * a = lm.Default(a, 3);
 */
lm.Default = function(variable, value)
{
	return (variable !== undefined) ? variable : value;
}

/**
 * Converts an rgb color string to a hex color string.
 * For example, "rgb(255, 0, 0) to 0xff0000"
 * @param {string} colorStr An rgb-style color string.
 * @returns {string} Returns the same color value as a 0x-style color string.
 */
lm.RgbStringToHexString = function(colorStr)
{ // https://stackoverflow.com/questions/13070054/convert-rgb-strings-to-hex-in-javascript
	// First cut away the CSS parts:
	let a = colorStr.split("(")[1].split(")")[0];
	// Then split it into separate numbers:
	a = a.split(",");
	// Convert the single numbers to hex:
	let b = a.map(function(x)
	{ // For each array element
		x = parseInt(x).toString(16); //Convert to a base16 string
		return (x.length == 1) ? "0" + x : x; // Add zero if we get only one character
	});
	// And glue it back together:
	b = "0x" + b.join("");
	return b;
}

/**
 * Converts an rgb color string to lm.RgbColor object.
 * For example, "rgb(255, 0, 0) to 0xff0000"
 * @param {string} colorStr An rgb-style color string.
 * @returns {lm.RgbColor} Returns the same color value as a color object.
 */
lm.RgbStringToRgbColor = function(colorStr)
{ // https://stackoverflow.com/questions/13070054/convert-rgb-strings-to-hex-in-javascript
	// First cut away the CSS parts:
	let a = colorStr.split("(")[1].split(")")[0];
	// Then split it into separate numbers:
	a = a.split(",");
	for (let i = 0; i < a.length; i++)
	{
		a[i] = parseInt(a[i]);
	}
	if (a.length == 3)
		return new lm.RgbColor(a[0], a[1], a[2]);
	else if (a.length == 4)
		return new lm.RgbColor(a[0], a[1], a[2], a[3]);
	else
		return new lm.RgbColor(0, 0, 0);
}

/**
 * Generates a UUID string.
 * @returns {string} A UUID string.
 */
lm.UUID = function()
{
	let d = new Date().getTime();
	let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c)
	{
		let r = (d + Math.random() * 16) % 16 | 0;
		d = Math.floor(d / 16);
		return (c == 'x' ? r : (r & 0x7 | 0x8)).toString(16);
	});
	return uuid;
}

/**
 * Removes punctuation from a string.
 * @param {string} str A string.
 * @returns {string} A new string with any punctuation characters removed.
 */
lm.RemovePunctuation = function(str)
{
	return str.replace(/[\.,-\/#!$%\^&\*;:{}=\-_`~()?]/g, "");
}

/**
 * Removes an extension from a filename.
 * @param {string} str A string.
 * @returns {string} A new string with any file extension removed.
 */
lm.RemoveFileExtension = function(str)
{
	return str.replace(/\.[^/.]+$/, "");
}

/**
 * Tests whether an image is loaded and valid.
 * @param {Image} img An image object.
 * @returns {boolean} True if the image is done loading and is valid, otherwise false.
 */
lm.IsImageValid = function(img)
{
	if (img.videoWidth)
	{
		if (img.videoHeight && img.readyState >= 3)
			return true;
		else
			return false;
	}
	else if (img.complete && img.naturalWidth !== undefined && img.naturalWidth > 0)
		return true;
	else
		return false;
}

/**
 * Formats a string and a variable number of arguments.
 * @param {string} str The format string.
 * @returns {string} Returns a string with the variable arguments inserted into place.
 * @example
 * lm.Format("And the %1 want to know whose %2 you %3", "papers", "shirt", "wear");
 * "And the papers want to know whose shirt you wear"
 */
lm.Format = function(str)
{
	let args = arguments;
	let pattern = new RegExp("%([1-" + arguments.length + "])", "g");
	return String(str).replace(pattern, function(match, index)
	{
		return args[index];
	});
}

/**
 * Randomly shuffles the elements of an array.
 * @param {number} array An array object.
 */
lm.ShuffleArray = function(array)
{
	let len = array.length;
	let shuffles = len * 3;
	for (let i = 0; i < shuffles; i++)
	{
		let item = array.pop();
		let pos = Math.floor(Math.random() * (len - 1));
		array.splice(pos, 0, item);
	}
}

function _dismissDropdownsOnClick(event)
{
	if (!event.target.matches('.dropbtn'))
	{
		let dropdowns = document.getElementsByClassName("dropdown-content");
		for (let i = 0; i < dropdowns.length; i++)
		{
			let openDropdown = dropdowns[i];
			if (openDropdown.classList.contains('show'))
			{
				if (openDropdown.classList.contains('dropdown-dialog'))
				{ // dropdown-dialog's should be harder to close
					if (!openDropdown.contains(event.target))
						lm.ToggleDropdown(openDropdown);
				}
				else
					lm.ToggleDropdown(openDropdown);
			}
		}
	}
}

/**
 * Adds a function to be called on the windows onload event. The benefit of using this function is that multiple functions can
 * be attached to the onload event, just by caling AddLoadEvent multiple times with each of the functions you want to run.
 * @param {function} func A function to call when the HTML page gets loaded.
 * @param {boolean} [makeFirst=false] If true, this callback function will be called before all others. This is used to set up the built-in console, but can be tricky and should mostly be avoided.
 */
lm.AddLoadEvent = function(func, makeFirst)
{
	let oldonload = window.onload;
	if (typeof(window.onload) != 'function')
	{
		window.onload = func;
	}
	else
	{
		window.onload = function()
		{
			if (makeFirst)
				func();

			if (oldonload)
			{
				oldonload();
			}

			if (!makeFirst)
				func();
		}
	}

	// Close any dropdowns if the user clicks outside of them
	window.onmousedown = _dismissDropdownsOnClick;
	if ('ontouchstart' in window)
		window.ontouchstart = _dismissDropdownsOnClick;
}

/**
 * Converts an object from one type to another. The type to convert to is passed in as a string.
 * Under the hood, a new object is created and the properties of the old object are copied to the new one.
 * @param {Object} existingObj A object to be converted.
 * @param {string} typeStr A string containing the type of object to convert it to.
 * @returns {Object} A new object of type "typeStr".
 */
lm.ConvertObjectType = function(existingObj, typeStr)
{
	let obj = eval("new " + typeStr + "();");
	for (let prop in existingObj)
	{
		if (existingObj.hasOwnProperty(prop))
		{
			obj[prop] = existingObj[prop];
		}
	}
	return obj;
}

/**
 * Restores an object (and any objects that it contains, recursively) to its "natural" object type.
 * You will likely never call this yourself, but it is used when Unserializing object data.
 * @param {Object} arg An object to be restored.
 * @returns {Object} A new object, or the same object if nothing needed to be changed.
 */
lm.RestoreObjectTypes = function(arg)
{
	let result = arg;

	if (result == null || typeof result != "object")
		return result;

	if (result._lmType_)
		result = lm.ConvertObjectType(result, result._lmType_);

	for (let prop in result)
	{
		if (prop.slice(0, 3) == "rt_" || prop.slice(0, 4) == "_rt_")
			continue; // skip runtime (rt_) properties
		if (result.hasOwnProperty(prop))
		{
			if (typeof result[prop] == "object")
			{
				result[prop] = lm.RestoreObjectTypes(result[prop]);
			}
		}
	}

	return result;
}

/**
 * Tags an object (and any objects that it contains, recursively) with its "natural" object type.
 * You will likely never call this yourself, but it is used when Serializing object data.
 * @param {Object} arg An object to be restored.
 * @returns {Object} A new object, or the same object if nothing needed to be changed.
 */
lm.TagObjectTypes = function(arg)
{
	let result = arg;

	if (result == null || typeof result != "object")
		return result;

	result._lmType_ = result.constructor.name;

	for (let prop in result)
	{
		if (prop.slice(0, 3) == "rt_" || prop.slice(0, 4) == "_rt_")
			continue; // skip runtime (rt_) properties
		if (result.hasOwnProperty(prop))
		{
			if (typeof result[prop] == "object")
			{
				result[prop] = lm.TagObjectTypes(result[prop]);
			}
		}
	}

	return result;
}

/**
 * Operates very similarly to JSON.stringify. Takes an object and serializes it as a string.
 * The difference from JSON.stringify is that the objects to be serialized may contain a special parameter
 * named "_lmType_". When Unserializing, such objects will be restored to their "natural" object type,
 * rather than being just flat, dumb objects as JSON would give you.
 * UPDATE: It is no longer necessary to tag objects with "_lmType_" - this gets handled automatically
 * @param {Object} arg An object or simple data that is to be serialized. If the object has a PreSerialize()
 * function, it will get called before the object is serialized.
 * @returns {string} A serialized string representing the argument.
 */
lm.Serialize = function(arg)
{
	if (arg.PreSerialize)
		arg.PreSerialize();
	lm.TagObjectTypes(arg);
	return JSON.stringify(arg, function(key, value)
	{
		if (key.slice(0, 3) == "rt_" || key.slice(0, 4) == "_rt_")
		{
			// Skip any values name rt_something or _rt_something
			// These are intended to be runtime values, and not something that should be serialized.
			// For example, suppose you have a complex graph of objects that point to each other.
			// You could end up with recursive, neverending serialization.
			// rt_parent could be used to hold a pointer to a parent object that doesn't need to be serialized.
			// (The _rt_something variation is intended for private variables.)
			return undefined;
		}
		return value;
	});

	// Below is a method of serializing functions.
	// But this only works if the function has been attached directly to the object, and not as part of a prototype.
	// This might be useful - maybe as an option passed to this function.
	/*return JSON.stringify(obj, function(key, value)
	{
		return (typeof value === "function") ? value.toString() : value;
	});*/
}

/**
 * Operates very similarly to JSON.parse. Takes a string and parses it into an object.
 * The difference from JSON.parse is that the objects being unserialized may contain a special parameter
 * named "_lmType_". When Unserializing, such objects will be restored to their "natural" object type,
 * rather than being just flat, dumb objects as JSON would give you.
 * @param {string} str A string to be unserialized.
 * @returns {Object} An object. If the object has a PostUnserialize()
 * function, it will get called after the object is unserialized.
 */
lm.Unserialize = function(str)
{
	let result = JSON.parse(str);

	// Below is a method of unserializing functions.
	// But this only works if the function has been attached directly to the object, and not as part of a prototype.
	// This might be useful - if I turn enable it in the Serialize function above I'll need to turn it on here as well.
	/*let result = JSON.parse(str, function(key, value)
	{
		if (typeof value != 'string')
			return value;
		return (value.substring(0,8) == 'function') ? eval('('+value+')') : value;
	});*/

	result = lm.RestoreObjectTypes(result);
	if (result.PostUnserialize)
		result.PostUnserialize();
	return result;
}

/**
 * Clones an object to make a totally new, independent copy of it.
 * If the object has its own clone method, that gets called.
 * @param {Object} o An object to be cloned. (Can be a number, string, array, or other things too.)
 * @returns {Object} A copy of the incoming object.
 */
lm.Clone = function(o)
{
	if (typeof(o) == "object")
	{
		if (Array.isArray(o))
		{
			let tempObj = {
				arrayValue: o
			};
			let json = lm.Serialize(tempObj);
			let copyObj = lm.Unserialize(json);
			return copyObj.arrayValue;
		}
		else
		{
			if (typeof(o.Clone) == "function")
				return o.Clone();
			else if (typeof(o.clone) == "function")
				return o.clone();
			else
			{
				let json = lm.Serialize(o);
				let copyObj = lm.Unserialize(json);
				return copyObj;
			}
		}
	}
	else if (typeof(o) == "string")
	{
		return o.slice();
	}
	return o;
}

/**
 * Loads a JSON file asynchronously and calls a callback function once it's ready.
 * @param {string} url An object to be cloned. (Can be a number, string, array, or other things too.)
 * @param {function} callback An object to be cloned. (Can be a number, string, array, or other things too.)
 * 
 * Sample Usage:
	lm.LoadJSON("json/Config.json", function(response)
	{
		// response is a JavaScript object, it has already been parsed from the JSON
		lm.Log(response);
	});
 */
lm.LoadJSON = function(url, callback)
{
	let xobj = new XMLHttpRequest();
	xobj.overrideMimeType("application/json");
	xobj.open('GET', url, true);
	xobj.onreadystatechange = function()
	{
		if (xobj.readyState == 4 && xobj.status == "200")
		{
			// Required use of an anonymous callback as .open will NOT return a value but simply returns undefined in asynchronous mode
			let parsedObject = lm.Unserialize(xobj.responseText);
			callback(parsedObject);
		}
	};
	xobj.send(null);  
}

